/**
 * @ngdoc directive
 * @name myApp.component.pow
 * @description
 * # pow
 */
angular.module('myApp.components.pow', [])
.directive('pow', function () {
  return {
    template: '<div></div>',
    restrict: 'E',
    link: function postLink(scope, element, attrs) {
      element.text('this is the pow directive');
    }
  };
});
